body {
  font-family: Roboto, sans-serif;
}

@media screen and (min-width: 320px), (max-width: 480) {
  .container {
    min-width: 320px;
    max-width: 428px;
    margin: auto;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}







:root {
  --primary-brand-color: #000;
}

header {
  background: var(--primary-brand-color);
}

a {
  text-decoration: none;
}

li {
  list-style-type: none;
}

.header_container {
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
}

.logo {
  align-items: center;
  display: flex;
}

.logo_img {
  width: 30px;
  height: 24px;
}

.logo_text {
  display: none;
}

@media screen and (max-width: 767px) {
  .header_menu_btn {
    text-transform: uppercase;
    color: #b7b7b7;
    background-color: rgba(0, 0, 0, 0);
    border: none;
    outline: none;
    font-size: 12px;
    font-weight: 500;
    line-height: 1.16;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }
}

.navbar_list {
  display: none;
}

#icon_moon, #icon_sun {
  width: 8px;
  height: 8px;
}

.mobile_menu_container {
  background-color: var(--primary-brand-color);
  border-radius: 0 8px 8px 0;
  padding: 20px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  top: 0;
  left: 0;
  transform: translate(-100%);
}

@media screen and (min-width: 768px) {
  .navbar_list {
    display: block;
  }

  .header_menu_btn {
    display: none;
  }

  a {
    color: #00f;
  }
}







.month-title {
  text-transform: uppercase;
  color: #000;
  margin: 0 0 28px;
  font-size: 28px;
  font-weight: 500;
  line-height: 1.18;
}

.month-item-title-text > div > h3 {
  color: #111;
  margin: 12px 0 0;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.19;
}

.month-item-title > h3:first-child {
  text-transform: uppercase;
  color: #111;
  font-size: 32px;
  font-weight: 500;
  line-height: 1.18;
}

.month-vote {
  margin-left: 62px;
}

.month-votes, .month-vote {
  width: 36px;
  height: 18px;
  text-align: center;
  text-transform: uppercase;
  color: #111;
  background: #f8f8f8;
  border-radius: 5px;
  padding: 1px 8px;
  font-size: 14px;
  line-height: 1.14;
}

.month-popular {
  text-transform: uppercase;
  color: #282828;
  align-items: center;
  margin-left: 78px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.19;
}

.month-genre {
  color: #282828;
  align-items: center;
  margin-left: 108px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.19;
}

.month-item-title > p {
  text-transform: uppercase;
  color: #111;
  align-items: center;
  margin: 20px 0 16px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.19;
}

.month-text {
  color: #282828;
  font-size: 14px;
  font-weight: 400;
}

.month-btn {
  width: 157px;
  height: 40px;
  text-align: center;
  color: #111;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%) border-box;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 74px;
  justify-content: center;
  align-items: center;
  margin: 24px 0 0;
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.14;
  display: flex;
}

.month-btn:-webkit-any(:hover, :focus) {
  background: none;
  border: 1px solid orange;
}

.month-btn:is(:hover, :focus) {
  background: none;
  border: 1px solid orange;
}

.month-item-img > img {
  display: block;
}

.month-item-img {
  border-radius: 5px;
  overflow: hidden;
}

.month-rel-date {
  text-transform: uppercase;
  color: #f87719;
  align-items: center;
  margin-left: 58px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.19;
}

@media screen and (min-width: 768px) {
  .month-item-title > h3:first-child {
    margin: 32px 0 24px;
  }

  .month-item-title-text {
    gap: 118px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .month-item-title > h3:first-child {
    margin: 0 0 28px;
  }

  .month-item-title-text {
    display: block;
  }

  .monthgalery {
    width: auto;
    flex-wrap: wrap;
    display: flex;
  }

  .month-item {
    flex-basis: 100%;
    gap: 16px;
    display: flex;
  }

  .month-item-title {
    max-width: 390px;
  }
}







